.header {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 47px 0 97px;

  @include respond-to(from0) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url('./../images/header-main/header320.jpg');
    
  }
  @include respond-to(tablet) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url('./../images/header-main/header768.jpg');
    padding: 40px 0 97px;
  }
  @include respond-to(desktop) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url('./../images/header-main/header1024.jpg');
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header.library {

  padding: 47px 0 65px;

  @include respond-to(from0) {
  background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url('./../images/header-library/library320.jpg');    
  }

  @include respond-to(tablet) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url('./../images/header-library/library768.jpg');
    padding: 40px 0 65px;
  }

  @include respond-to(desktop) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url('./../images/header-library/library1024.jpg');
  }
}

.logo {
  animation: lightSpeedInLeft;
  animation-duration: 1.5s;

  @include respond-to(tablet) {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__icon {
    display: inline-block;
    // width: 24px;
    // height: 24px;
    stroke: getColor(logo-icon-color);
  }
  &__text {
    display: none;
    @include respond-to(tablet) {
      display: block;
      font-weight: 500;
      @include font(30px, 35px);
      color: getColor(logo-title-color);
      margin-left: 10px;
    }
  }
}

.nav {
  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__item:nth-child(2) {
    margin-left: 40px;
  }
  &__item.active {
    position: relative;

    &::after {
      position: absolute;
      content: '';
      display: block;
      width: 100%;
      height: 3px;
      background: getColor(accent);
    }
  }
  &__link {
    font-weight: 500;
    @include font(12px, 14px);
    text-transform: uppercase;
    color: getColor(link-text-color);

    &:hover,
    &:focus {
      color: getColor(accent);
    }
  }
}

.input {
  width: 100%;
  height: 20px;
  font-weight: 400;
  @include font(14px, 16px);
  color: getColor(input-searchquery);
  background-color: transparent;
  border: none;
  border-bottom: 0.5px solid getColor(input-border-color);
  padding: 0 15px 4px 1px;
  outline: none;

  @include respond-to(mobile) {
    width: 280px;
  }

    @include respond-to(tablet) {
    width: 336px;
  }

  &:focus {
    border-bottom: 0.5px solid getColor(accent);
  }
  &::placeholder {
    font-weight: 400;
    @include font(14px, 16px);
    color: getColor(input-placeholder-color);
  }
  &__wrapper {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 38px;

    &.page-inactive {
      display: none;
    }
  }
  &__search-btn {
    position: absolute;
    content: '';
    bottom: 6px;
    right: 0;
    background: none;
    border: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    stroke: getColor(input-search-icon);

    @include respond-to(tablet) {
      right: (142px);
    }

    @include respond-to(desktop) {
      right: (274px);
    }
  }
  &__error {
    position: absolute;
    content: '';
    top: 44px;
    left: 0;
    font-weight: 400;
    @include font(14px, 16px);
    color: getcolor(error-color);

    @include respond-to(tablet) {
      left: (113px);
    }

    @include respond-to(desktop) {
      left: (244px);
    }
  }
}

// Retina ....................
@media screen and (min-device-pixel-ratio: 2) and (max-width: 767px),
  screen and (min-resolution: 192dpi) and (max-width: 767px),
  screen and (min-resolution: 2dppx) and (max-width: 767px) {
  .header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('./../images/header-main/header320@2x.jpg');
  }
  .header.library {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('./../images/header-library/library320@2x.jpg');
  }
}

@media screen and (min-device-pixel-ratio: 2) and (min-width: 768px),
  screen and (min-resolution: 192dpi) and (min-width: 768px),
  screen and (min-resolution: 2dppx) and (min-width: 768px) {
  .header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('./../images/header-main/header768@2x.jpg');
  }
  .header.library {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('./../images/header-library/library768@2x.jpg');
  }
}

@media screen and (min-device-pixel-ratio: 2) and (min-width: 1024px),
  screen and (min-resolution: 192dpi) and (min-width: 1024px),
  screen and (min-resolution: 2dppx) and (min-width: 1024px) {
  .header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('./../images/header-main/header1024@2x.jpg');
  }
  .header.library {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('./../images/header-library/library1024@2x.jpg');
  }
}

// library
.library-button {
  font-weight: 500;
  @include font(12px, 16px);
  width: 130px;
  height: 44px;
  color: getColor(link-text-color);
  background-color: transparent;
  outline: none;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 20px;
  }
  
  &.is-active {
    background-color: getColor(accent);
    box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
    border: none;
  }

  @include respond-to(tablet) {
    width: 136px;
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}

.library-button__wrapper {
  display: flex;
  text-align: center;
  justify-content: center;
  margin-top: 46px;

  &.page-inactive {
    display: none;
  }
}


